import React from 'react';
import Project from '../layout/Project';
import { JhcProjects } from '../../data/Projects';

const Jhc = () => {
  return (
    <div className='container my-5'>
      {JhcProjects.map((prj) => (
        <Project {...prj} />
      ))}
    </div>
  );
};

export default Jhc;
