import React from 'react';
import Project from '../layout/Project';
import { MernProjects } from '../../data/Projects';

const MernStack = () => {
  return (
    <div className='container my-5'>
      {MernProjects.map((prj) => (
        <Project {...prj} />
      ))}
    </div>
  );
};

export default MernStack;
