import React from 'react';
import Project from '../layout/Project';
import { PwaReactProjects } from '../../data/Projects';

const PwaReact = () => {
  return (
    <div className='container my-5'>
      {PwaReactProjects.map((prj) => (
        <Project {...prj} />
      ))}
    </div>
  );
};

export default PwaReact;
